export default{
	data(){
		return{
			mytrue:false,//是否登录
		}
	},
	onNavigationBarButtonTap(e){
		console.log("监听到原生标题栏按钮点击事件");
		console.log(e);
		if(e.index == 0){
			//消息
			uni.navigateTo({
				url:'Inform/inform'
			})
		}else{
			//设置
			uni.navigateTo({
				url:'Seeting/seeting'
			})
		}
	},
	methods:{
		//点击登录
		loginClick(){
			uni.navigateTo({
				url:'../login/login'
			})
		},
		//商品收藏
		collectClick(){
			uni.navigateTo({
				url:'MyshopMessage/collect'
			})
		},
		//店铺收藏
		attrntionClick(){
			uni.navigateTo({
				url:'MyshopMessage/attention'
			})
		},
		//积分商城
		integralShopCick(){
			uni.navigateTo({
				url:'../Home/Integral/integralShop'
			})
		},
		//优惠券
		discountsClick(){
			uni.navigateTo({
				url:'Discounts/mydiscounts'
			})
		},
		//我的订单
		myOrderClick(num){
			uni.navigateTo({
				url:'MyOrder/myorder?index=' + num
			})
		},
		//退款
		refundClick(){
			uni.navigateTo({
				url:'MyBooking/refund/refund'
			})
		},
		//我的拼团
		MybookingClick(){
			uni.navigateTo({
				url:'MyBooking/mybooking'
			})
		},
		//商家入驻
		enterClick(){
