				url:'Enter/enter'
			})
		},
		//我的地址
		addressClick(){
			uni.navigateTo({
				url:'address/AllAddress'
			})
		},
		//积分明细
		intrgralDetailClick(){
			uni.navigateTo({
				url:'../Home/Integral/intrgralDetail'
			})
		},
		//领券中心
		discountscententClick(){
			uni.navigateTo({
				url:'Discounts/DiscpuntsCentent'
			})
		}
	}
